import { Action } from 'redux';
import { channel } from 'redux-saga';
import { all, call, put, takeEvery } from 'redux-saga/effects';
import { IScrapConfig, scrap } from '~/ui/scrap/parsex/parsex-ql';
import { setLS } from '~/ui/utils/LocalUserInfo';
import logger from '~/ui/utils/logger';
import { createMutationObserver } from '~/ui/utils/observation.utils';
import { getExtensionPage } from '~/ui/utils/urlAccess';
import browser from '~B/browser/browser';
import { LoginCredentials } from '~B/storage/storage.types';
import {
    ExtensionScrappingWorkingMode,
    ScrappedData,
    ShopDetailMatcher,
    ShopScrappingDetails,
} from '~B/types';
import { ILoginStatus, setLogInLoader, setLoginStatus } from '~F/account/redux/account';
import { IOffer } from '~F/pepper-posts/redux/offer';
import { setPostedOffer } from '~F/pepper-posts/redux/posted-offer/postedOffer.reducer';
import { setUnLock } from '~F/pepper-posts/redux/voucher/voucher.reducer';
import { IPriceHistoryPoint, IProductInfo } from '~F/product-stats/redux';
import { setHistory, setProduct } from '~F/product-stats/redux/product-stats/product-stats.reducer';

import { metricViews } from '~F/app/api/metrics/metricsViews.api';
import {
    DISABLE_MARKER,
    disableMarkerSaga,
} from '~F/price-track/redux/PriceTrackNotifications.saga';

const ns = 'background-link/';

export const FETCH_PRODUCT_STATS_RESULT = `${ns}fetch-product-stats-result`;
export const SCRAP_CONFIG_MATCH = `${ns}scrap-config-match`;
export const SCRAPPED_DATA = `${ns}scrapped-data`;
export const Q_SIMPLE_LOGIN = `${ns}query-simple-login`;
export const A_SIMPLE_LOGIN = `${ns}answer-simple-login`;
export const UNLOCK_VOUCHER = `${ns}unlock-voucher`;
export const START_APP = `${ns}start-app`;
export const LOGOUT = `${ns}logout`;
export const RELOAD_EXTENSION = `${ns}reload-extension`;
export const RELEASED_OFFER = `${ns}released-offer`;
export const CREATE_TAB = `${ns}-create-tab`;

export function createTab(pageName: string) {
    return {
        type: CREATE_TAB,
        payload: getExtensionPage(pageName),
    };
}
export function logOut() {
    return {
        type: LOGOUT,
    };
}

export function startUIApp() {
    return {
        type: START_APP,
    };
}

export function fetchProductStatsResult(p: IProductStatsData) {
    return {
        type: FETCH_PRODUCT_STATS_RESULT,
        payload: p,
    };
}

export function scrapConfigMatch(p?: ShopDetailMatcher) {
    return {
        type: SCRAP_CONFIG_MATCH,
        payload: p,
    };
}

export function scrappedData(p: { data: any; url: string; config: ShopDetailMatcher }) {
    return {
        type: SCRAPPED_DATA,
        payload: p,
    };
}
export function releasedOffert(p: IOffer) {
    return {
        type: RELEASED_OFFER,
        payload: p,
    };
}
export function querySimpleLogin(p?: LoginCredentials) {
    return {
        type: Q_SIMPLE_LOGIN,
        payload: p,
    };
}

export function answerSimpleLogin(loginStatus: ILoginStatus) {
    return {
        type: A_SIMPLE_LOGIN,
        payload: loginStatus,
    };
}
export function unlockVoucherActionCreator(payload: boolean) {
    return {
        type: UNLOCK_VOUCHER,
        payload,
    };
}

export function* startAppSaga() {
    yield put(setRenderStatus(true));
}

export interface IProductStatsData {
    shop: string | null;

    product: IProductInfo;
    priceHistory: IPriceHistoryPoint[];
}

export function* receiveProductStatsSaga({ type, payload }: PayloadAction<IProductStatsData>) {
    try {
        switch (type) {
            case FETCH_PRODUCT_STATS_RESULT:
                yield put(setProduct(payload.product));
                yield put(setHistory(payload.priceHistory));
                break;
        }
    } catch (e) {
        logger.error('background-saga', { exception: e });
    }
}
export function* releasedOffertSaga({ type, payload }: PayloadAction<IOffer>) {
    yield put(setPostedOffer(payload));
}
export function* querySimpleLoginSaga(a: any) {
    yield put(setLogInLoader(true));
    yield call(browser.runtime.sendMessage, a);
}

export function* answerSimpleLoginSaga({ payload }: { payload: ILoginStatus }) {
    const localUserKey = 'loginStatus';

    yield put(setLoginStatus(payload));
    yield put(setLogInLoader(false));

    setLS(localUserKey, payload);
}
export function* logOutSaga(action: Action) {
    yield call(browser.runtime.sendMessage, action);
}

export let lastScrappedObject: ScrappedData | null = null;

export function* runScrapperSaga({ payload }: { payload: ShopDetailMatcher }) {
    const config: IScrapConfig = {
        dataScrap: payload.scrapperConfig,
    };

    logger.debug('Running scrapper');

    try {
        // data scrapping
        let scrappedObject = (yield call(urlImgValidatorLoop, config)) as ScrappedData;
        if (lastScrappedObject) {
            let foundInequality = false;
            for (const key of Object.keys(scrappedObject)) {
                if (lastScrappedObject[key] !== scrappedObject[key]) {
                    foundInequality = true;
                    break;
                }
            }
            if (!foundInequality) {
                logger.info('Skipped scrappedObject');
                return;
            }
        }

        lastScrappedObject = scrappedObject;
        // start loading state
        const message = scrappedData({
            data: { ...scrappedObject, shop: payload.shop },
            url: location.href,
            config: payload,
        });

        yield call(browser.runtime.sendMessage, message);
        metricViews.methods.metricView({ name: 'ext-event-load' });
    } catch (e) {
        logger.debug('scraptConfigMatchSaga error', { exception: e });
        yield put(setRenderStatus(shouldLoadUI(payload)));
    }
}

const scrapConfigMatchChannel = channel();

const scrapConfigObserver = !browser.runtime ? createMutationObserver('body', () => {}) : null;
scrapConfigObserver?.observe();

export function* scrapConfigMatchSaga(action: PayloadAction<ShopDetailMatcher>) {
    lastScrappedObject = null;
    scrapConfigObserver?.changeCallback((m) => {
        scrapConfigMatchChannel.put(action);
    });
    scrapConfigMatchChannel.put(action);
}

export function* unlockVoucherSaga({ payload }: { type: string; payload?: any }) {
    yield call(browser.runtime.sendMessage, unlockVoucherActionCreator(payload));
    yield put(setUnLock(true));
}

export function* appLoadingSaga() {}

export function* watchBackgroundSaga() {
    yield all([
        //takeEvery(START_APP, startAppSaga),
        takeEvery(FETCH_PRODUCT_STATS_RESULT, receiveProductStatsSaga),
        takeEvery(SCRAP_CONFIG_MATCH as any, scrapConfigMatchSaga),
        takeEvery(scrapConfigMatchChannel, runScrapperSaga),
        takeEvery(UNLOCK_VOUCHER, unlockVoucherSaga),
        takeEvery(RELEASED_OFFER, releasedOffertSaga),
        takeEvery(DISABLE_MARKER, disableMarkerSaga),
    ]);
}

// TODO future input validation to accetp IProductInfo[] too
function isValidUrlImg(product: ScrappedData) {
    if (typeof product?.imageUrl === 'string') return product.imageUrl.startsWith('data:');
    return false;
}
type ScrapCheckConfig = {
    attemptsNumber: number;
    attemptDuration: number;
    maxCheckDuration: number;
};
async function urlImgValidatorLoop(
    scrapConfig: IScrapConfig,
    { attemptsNumber, attemptDuration, maxCheckDuration }: ScrapCheckConfig = {
        attemptsNumber: 3,
        attemptDuration: 350,
        maxCheckDuration: 2000,
    },
) {
    let scrappProductData = scrap(scrapConfig);
    let attemptIterationCount = 0;
    while (
        attemptIterationCount < attemptsNumber &&
        attemptDuration * attemptIterationCount < maxCheckDuration
    ) {
        if (isValidUrlImg(scrappProductData as ScrappedData)) {
            return scrappProductData;
        } else {
            await awaitAction(attemptDuration);
            scrappProductData = scrap(scrapConfig);
        }
        attemptIterationCount += 1;
    }
    return scrappProductData;
}

function awaitAction(timeout: number) {
    return new Promise((resolve) => {
        return setTimeout(resolve, timeout);
    });
}

export function shouldLoadUI(config: ShopScrappingDetails, scrappedObject?: ScrappedData): boolean {
    switch (config?.workingMode) {
        case ExtensionScrappingWorkingMode.onlyId:
            return true;

        case ExtensionScrappingWorkingMode.disabled:
            return false;

        default:
            return true;
    }
}
