import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IPriceHistoryPoint, IPriceState, IProductInfo, IStats } from './product-stats.types';
import { calculateStats } from './product-stats.utils';

const initialState: IPriceState = {
    history: [],
    stats: {
        maxPrice: 0,
        meanPrice: 0,
        minPrice: 0,
        price: 0,
    },
    product: null,
};

const priceSlice = createSlice({
    name: 'price',
    initialState,
    reducers: {
        setProduct(state, action: PayloadAction<IProductInfo>) {
            if (!action.payload) return;
            state.product = action.payload;
            state.stats.price = action.payload.price;
        },
        setStats(state, action: PayloadAction<IStats>) {
            if (!action.payload) return;
            state.stats.price = action.payload.price;
            calculateStats(state);
        },
        setHistory(state, action: PayloadAction<IPriceHistoryPoint[]>) {
            if (!action.payload) return;
            state.history = action.payload;
            calculateStats(state);
        },
    },
});

export default priceSlice.reducer;
export const { setProduct, setStats, setHistory } = priceSlice.actions;
