import { createAPI } from '~/ui/utils/api';
import { IUserConfig } from './user-config.types';

const defaultConfig: IUserConfig = {
    notifyByEmail: true,
    trackingEmail: '',
    notifyByDm: true,
};

export const configAPIHandler = createAPI({
    name: 'user-config',
    endpoint: 'user/config',
    default() {
        return defaultConfig;
    },
    async read(): Promise<IUserConfig> {
        const config = await this.fetch('')
            .then((v) => v.json())
            .then((v) => (v.data as IUserConfig) || defaultConfig);
        return config;
    },
    methods: {
        async update(config: IUserConfig) {
            return this.fetch('', { method: 'POST', body: JSON.stringify(config) });
        },
    },
});
