import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ShopScrappingDetails } from '~B/types';
import {
    IDebugState,
    INewConfPayload,
    IRemoveConfPayload,
    IUpdateConfPayload,
} from './debug.types';
import { sampleState } from './debug.sample';

const initialState: IDebugState =
    import.meta.env.VITE_NODE_ENV === 'test'
        ? sampleState
        : {
              viewScrapped: false,
              scrappedObject: {},
              shopScrappingDetails: {},
              isLocalConfig: false,
          };

const debugSlice = createSlice({
    name: 'debug',
    initialState,
    reducers: {
        setViewScrapped(state, { payload }: PayloadAction<boolean>) {
            state.viewScrapped = payload;
        },
        setScrapperConfig(state, { payload }: PayloadAction<ShopScrappingDetails>) {
            state.shopScrappingDetails = payload;
        },
        setAddNewScrapConf(state, { payload }: PayloadAction<INewConfPayload>) {
            (state.shopScrappingDetails.scrapperConfig[payload.scrapKey] as string[]).push(
                payload.scrapConf,
            );
        },
        setRemoveConf(state, { payload }: PayloadAction<IRemoveConfPayload>) {
            (state.shopScrappingDetails.scrapperConfig[payload.scrapKey] as string[]).splice(
                payload.confIndex,
                1,
            );
        },
        setUpdateConf(state, { payload }: PayloadAction<IUpdateConfPayload>) {
            (state.shopScrappingDetails.scrapperConfig[payload.scrapKey] as string[])[
                payload.scrapConfIndex
            ] = payload.scrapConf;
        },
        setIsLocalConfig(state, { payload }: PayloadAction<boolean>) {
            state.isLocalConfig = payload;
        },
    },
});

export default debugSlice.reducer;
export const {
    setViewScrapped,
    setScrapperConfig,
    setAddNewScrapConf,
    setRemoveConf,
    setUpdateConf,
    setIsLocalConfig,
} = debugSlice.actions;
