import { call, put, takeEvery } from '@redux-saga/core/effects';
import logger from '~/ui/utils/logger';
import { ILoginStatus } from '~F/account/redux/account';
import { setIconCount } from '~F/app/redux/app';
import { A_SIMPLE_LOGIN } from '~F/app/redux/app/background-link.saga';
import { IPriceTrackNotification, NotificationStatus, priceTrackNotificationsAPI } from '../api';
import browser from '~B/browser/browser';

export function* priceTrackNotificationSaga() {
    const isPinfoOn = JSON.parse(import.meta.env.VITE_FT_PI as string);
    if (!isPinfoOn) return;
    yield takeEvery(DISABLE_MARKER, disableMarkerSaga);
    yield takeEvery(A_SIMPLE_LOGIN as any, checkNotiFicationStatusSaga);
}

const unreadNotificationStatuses = new Set([
    NotificationStatus.Notified,
    NotificationStatus.PendingEmail,
]);

export function* checkNotiFicationStatusSaga({ payload: { loggedIn } }: { payload: ILoginStatus }) {
    if (!loggedIn) {
        yield put(setIconCount({ count: 0, icon: 'account' }));
        return;
    }
    try {
        // get notifications
        const notifications: IPriceTrackNotification[] = yield call(
            priceTrackNotificationsAPI.read,
        );
        // unset ball from account barIcon
        const unreadNotifications = notifications.filter((v) =>
            unreadNotificationStatuses.has(v.status),
        );

        const unreadCount = unreadNotifications.length;
        yield put(setIconCount({ count: unreadCount, icon: 'account' }));
    } catch (e: any) {
        logger.warn('Cannot get notifications', e);
    }
}
export function* disableMarkerSaga() {
    try {
        yield call(priceTrackNotificationsAPI.methods.markAll);
    } catch (e: any) {
        logger.warn(`Error apt notifications api: ${e.message}`);
    }
}
export function* removeNotificationSaga(payload: { id: string }) {
    yield call(priceTrackNotificationsAPI.methods.remove, payload.id);
}
export const DISABLE_MARKER = 'DISABLE_MARKER';
export const REMOVE_NOTIFICATION = 'REMOVE_NOTIFICATION';
export const CHECK_NOTIFICATION_STATUS = 'CHECK_NOTIFICATION_STATUS';
export function disableMarker() {
    return {
        type: DISABLE_MARKER,
    };
}
export function removeNotification(id: string) {
    return {
        type: REMOVE_NOTIFICATION,
        payload: id,
    };
}
export function checkNotiFicationStatus() {
    return {
        type: CHECK_NOTIFICATION_STATUS,
    };
}
