import { createAPI, metricsOptions } from '~/ui/utils/api';

const methods = {
    metricPepperToChollo(pepperMetrics: metricsOptions['body']) {
        this.metric({
            body: {
                name: pepperMetrics.name,
                data: pepperMetrics.data,
            },
            method: 'POST',
        });
    },
};

export const metricPepper = createAPI({
    name: 'metrics:pepper',
    methods,
    endpoint: 'metric/',
    read: () => {},
});
