import { createAPI } from '~/ui/utils/api';
import browser from '~B/browser/browser';
import { IPriceTrackNotification } from './price-track-notifications.types';

export const priceTrackNotificationsAPI = createAPI({
    name: 'price-track-notifications',
    endpoint: 'price-track/notifications/',
    default: () => [],
    async read() {
        return this.fetch('')
            .then((v) => {
                return v.json();
            })
            .then((v) => {
                const notifications = (
                    v.data as IPriceTrackNotification[]
                ).map<IPriceTrackNotification>((ptnot) => {
                    ptnot.product.id = (ptnot.product as any)['productInfoId'];
                    return ptnot;
                });

                return notifications;
            });
    },
    methods: {
        async remove(priceTrackNotificationId: string) {
            return this.fetch(`${priceTrackNotificationId}/delete`, {
                method: 'DELETE',
            }).then((v) => v.json());
        },
        async markAll() {
            return this.fetch('all/read', { method: 'POST' })
                .then((v) => v.json())
                .then(() => {
                    browser.browserAction.setBadgeText({ text: '' });
                });
        },
    },
});
