import logger from '~/ui/utils/logger';
import { ScrapperConfig } from '~B/types';
import { IProductInfo } from '~F/product-stats/redux';
import { createExecutor, parser } from '.';
import * as PROCESSORS from './parsex-ql-processors';
import { ASTError, ASTPipeArg, ASTResult, ASTValue } from './types';
export { parser } from '.';

export const execute = createExecutor(PROCESSORS);

type RunOptions = {
    stackErrors?: boolean;
    debug?: boolean;
    initialValue?: ASTPipeArg;
};

const defaultRunOptions: RunOptions = {
    stackErrors: false,
    debug: false,
    initialValue: null,
};

export function runQuery(query: string, opts: RunOptions = defaultRunOptions): ASTResult {
    try {
        opts = { ...defaultRunOptions, ...opts };
        const result = parser.run(query);
        if (result.isError) {
            console.error(result);
            return {
                error: true,
                kind: 'ExecutionError',
                message: `Parse: ${result.error}`,
                stackedErrors: [],
            };
        } else {
            if (opts.debug) {
                logger.log('Executing: ', { result: (result as any)?.result });
            }
            const exResult = execute((result as any)?.result, opts.initialValue);
            if (exResult) {
                return exResult;
            } else {
                return {
                    error: true,
                    kind: 'ExecutionError',
                    message: `NoOutputValue`,
                    stackedErrors: [],
                };
            }
        }
    } catch (error) {
        if (opts.debug) {
            console.error('parser error', error.message);
        }
        return {
            error: true,
            kind: 'ExecutionError',
            message: `Unknown: ${error}`,
            stackedErrors: [],
        };
    }
}

export function runCascadeQuery(
    cascadeQuery: string[],
    opts: RunOptions = defaultRunOptions,
): ASTResult {
    opts = { ...defaultRunOptions, ...opts };
    const stackedErrors: ASTError[] = [];
    for (const query of cascadeQuery) {
        const result: any = runQuery(query, opts);
        if (result?.error) {
            if (opts.stackErrors)
                stackedErrors.push({
                    error: true,
                    kind: 'ExecutionError',
                    message: `Cascading down to next execution due to error`,
                    stackedErrors: [result],
                });

            continue;
        }

        if (result?.value === null) {
            if (opts.stackErrors)
                stackedErrors.push({
                    error: true,
                    kind: 'ExecutionError',
                    message: `Cascading down to next execution due to null value`,
                    stackedErrors: [],
                });
            continue;
        }

        return opts.stackErrors ? { ...result, stackedErrors } : result;
    }
    return {
        error: true,
        kind: 'ExecutionError',
        message: `Cascade resolves to null`,
        stackedErrors,
    };
}

export function runParallel<T>(runner: (query: T, RunOptions?: RunOptions) => ASTResult) {
    return (queryObject: ScrapperConfig, opts: RunOptions = defaultRunOptions) => {
        opts = { ...defaultRunOptions, ...opts };
        const resultObject: { [key in keyof IProductInfo]: ASTPipeArg } = {} as {
            [key in keyof IProductInfo]: ASTPipeArg;
        };
        for (const key in queryObject) {
            let scrapResult = runner(queryObject[key], opts);
            resultObject[key] = scrapResult.error ? null : scrapResult.value;
        }
        return resultObject;
    };
}

export const runQueries = runParallel<string>(runQuery);
export const runCascadeQueries = runParallel<string[]>(runCascadeQuery);

