export interface IScrapConfig<T = any> {
    stackErrors?: boolean;
    debug?: boolean;
    baseSelect?: string[];
    dataScrap: ScrapperConfig;
}

export function scrap(config: IScrapConfig) {
    if (!config.baseSelect) {
        return runCascadeQueries(config.dataScrap, {
            stackErrors: config.stackErrors,
            debug: config.debug,
        });
    }
    const errors: ASTError[] = [];
    const base = runCascadeQuery(config.baseSelect, {
        stackErrors: config.stackErrors,
        debug: config.debug,
    });
    if (config.debug) logger.log('Base:', base);
    if (base.error) return base;

    if (Array.isArray((base as ASTValue).value)) {
        const list = (base as ASTValue).value as HTMLElement[];
        const acc: { [k in keyof IProductInfo]: ASTPipeArg }[] = [];
        for (const el of list) {
            if (!(el instanceof HTMLElement)) {
                if (config.stackErrors)
                    errors.push({
                        error: true,
                        kind: 'ExecutionError',
                        message: `Element [${el}] is not a HTMLElement`,
                        stackedErrors: [],
                    });
                continue;
            }
            acc.push(
                runCascadeQueries(config.dataScrap, {
                    stackErrors: config.stackErrors,
                    debug: config.debug,
                    initialValue: el,
                }),
            );
        }
        return acc;
    }

    return runCascadeQueries(config.dataScrap, {
        initialValue: (base as ASTValue).value,
    });
}
