import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { addExtensionUTMParamsToLink } from '~/ui/utils/UTMLinks';
import { IOffer } from '../offer/offer.types';

export interface IPostedOfferState {
    offer: IOffer | null;
}

const initialState: IPostedOfferState = {
    offer: null,
};

const postedOfferSlice = createSlice({
    name: 'offer',
    initialState,
    reducers: {
        setPostedOffer(state, action: PayloadAction<IOffer>) {
            if (action.payload) {
                action.payload.url = addExtensionUTMParamsToLink(
                    action.payload?.url,
                    'extension_ofertapublicada',
                );
            }
            state.offer = action.payload;
        },
    },
});

export default postedOfferSlice.reducer;
export const { setPostedOffer } = postedOfferSlice.actions;
