import {
    between,
    char,
    choice,
    coroutine,
    digits,
    fail,
    letters,
    many,
    namedSequenceOf,
    optionalWhitespace,
    possibly,
    recursiveParser,
    regex,
    sepBy,
    sequenceOf,
} from 'arcsecond';

class JSONRegExp extends RegExp {
    constructor(regExp: string, flags?: string) {
        if (regExp[0] === '/' && regExp[0] == regExp[regExp.length - 1]) {
        }
        super(regExp, flags);
    }
    toJSON() {
        return this.toString();
    }
}

const surroundedBy = (parser) => between(parser)(parser);
const betweenSquareBrackets = between(char('['))(char(']'));
const betweenParens = between(char('('))(char(')'));
const pipeSeparated = sepBy(surroundedBy(optionalWhitespace)(char('|')));
const commaSeparated = sepBy(surroundedBy(optionalWhitespace)(char(',')));

const flags = namedSequenceOf([
    ['flat', possibly(char('_'))],
    ['multi', possibly(char('!'))],
]);
const secondName = possibly(
    choice([
        flags,
        namedSequenceOf([
            ['secondName', letters],
            ['flags', possibly(flags)],
        ]),
    ]),
);
const extractFlags = ({ flat, multi }: { flat?: string; multi?: string }) => {
    if (!flat && !multi) return '';
    if (flat && multi) return flat + multi;
    else if (flat && !multi) return flat;
    else return multi;
};
const checkFlags = (value: any, flag: string) => {
    if (value.fSecond && value.fSecond.flags) {
        return value.fSecond.flags[flag] ? true : false;
    }
    if (value.flags) {
        return value.flags[flag] ? true : false;
    }
};
const fnName = namedSequenceOf([
    ['fName', letters],
    ['flags', flags],
    [
        'fSecond',
        possibly(
            namedSequenceOf([
                ['secondName', letters],
                ['flags', flags],
            ]),
        ),
    ],
]).map((value: any) => ({
    kind: 'symbol',
    type: 'name',
    value: `${(() => {
        if (value.fSecond) {
            const { fSecond } = value;
            return value.fName + '_' + fSecond.secondName;
        }
        return value.fName;
    })()}`,
    multi: checkFlags(value, 'multi'),
    flatten: checkFlags(value, 'flat'),
}));
const stringAux = regex(/^([^\/\)])*/).map((value) => {
    /* if (value === '') {
        return fail('string could not contain these chars / or (');
    } */
    return {
        kind: 'lit',
        type: 'stringAux',
        value: value,
    };
});

const fn = sequenceOf([
    fnName,
    betweenParens(
        choice([
            // This comment is the future support once all queries are updated
            // The engine implementation supports this, but current queries do not.
            commaSeparated(recursiveParser(() => expression)),

            // From here should be commented out once full literal support is achieved

            // recursiveParser(() => argument),
            // stringAux,
        ]),
    ),
]).map((value: any) => {
    return {
        kind: 'fn',
        fn: value[0].value,
        multi: value[0].multi,
        flatten: value[0].flatten,
        args: value[1].kind ? [value[1]] : [...value[1]],
    };
});

const regularExpression = sequenceOf([
    surroundedBy(char('/'))(regex(/^([^\/]|(?<=\\)\/)*/)),
    possibly(many(choice([...'gmisuy'].map((c) => char(c))))),
]); /* .map((value) => {
    const regex = new JSONRegExp(`${value[0]}`, value[1]?.join('') || undefined);
    return {
        kind: 'lit',
        type: 'regex',
        value: regex,
    };
}); */
const regexParser = coroutine(function* () {
    const regex = yield regularExpression;
    try {
        const regexResult = new JSONRegExp(`${regex[0]}`, `${regex[1].join('')}` || undefined);

        return {
            kind: 'lit',
            type: 'regex',
            value: regexResult,
        };
    } catch (error) {
        const exitError = yield fail(error.message);
        return exitError;
    }
});
const nullValue = sequenceOf([...'null'].map((c) => char(c))).map((v) => ({
    kind: 'lit',
    type: 'null',
    value: null,
}));

const string = surroundedBy(char('`'))(regex(/^([^`])*/)).map((value) => ({
    kind: 'lit',
    type: 'string',
    value: value,
}));

const integer = digits.map((value) => ({
    kind: 'lit',
    type: 'integer',
    value: parseInt(value),
}));

const float = sequenceOf([digits, char('.'), digits]).map((value) => ({
    kind: 'lit',
    type: 'float',
    value: parseFloat(value.join('')),
}));

const argument = choice([
    nullValue,
    string,
    float,
    integer,
    regexParser,

    recursiveParser(() => list),
    recursiveParser(() => obj),
]);

const list = betweenSquareBrackets(commaSeparated(argument)).map((value: any) => ({
    kind: 'lit',
    type: 'list',
    value: value.map((ast) => {
        return ast.value;
    }),
}));

const obj = between(char('{'))(char('}'))(
    surroundedBy(optionalWhitespace)(
        commaSeparated(
            sequenceOf([letters, surroundedBy(optionalWhitespace)(char(':')), argument]),
        ).map((value: any[]) => ({
            kind: 'lit',
            type: 'object',
            value: value.reduce((acc, curr) => ((acc[curr[0]] = curr[2].value), acc), {}),
        })),
    ),
);

const pipeElement = recursiveParser(() => fn);

const pipe = choice([
    pipeSeparated(pipeElement).map((values) => ({
        kind: 'pipe',
        values: values,
    })),
]);

const expression = choice([
    sequenceOf([
        argument,
        possibly(sequenceOf([surroundedBy(optionalWhitespace)(char('|')), pipe])),
    ]).map(([argument, pipe]: any[]) => {
        [, pipe] = pipe || [];
        const pipeArgs = pipe?.values || [];
        return {
            kind: 'pipe',
            values: [argument, ...pipeArgs],
        };
    }),
    pipe,
]);

export const parser = between(optionalWhitespace)(optionalWhitespace)(expression);
export default parser;
