import {
    ASTArg,
    ASTElement,
    ASTError,
    ASTPipeArg,
    ASTResult,
    ASTValue,
    TransformerFn,
} from './types';

export function createExecutor(processors: { [k: string]: TransformerFn }) {
    return (ast: ASTElement, initialValue: ASTPipeArg = null) =>
        executeAST(ast, initialValue, processors);
}

function executeAST(
    ast: ASTElement,
    initialValue: ASTPipeArg = null,
    processors: { [k: string]: TransformerFn } = {},
): ASTResult<ASTPipeArg> {
    switch (ast?.kind) {
        case 'lit':
            return {
                value: ast.value,
            };
        case 'pipe':
            return ast.values.reduce<ASTResult<ASTPipeArg>>(
                (acc, curr) => {
                    if (acc?.error) return acc;
                    const res = executeAST(curr, (acc as ASTValue)?.value, processors);
                    return res;
                },
                { value: initialValue } as any as ASTResult<ASTPipeArg>,
            );

        case 'fn':
            const args = ast.args.map((arg) => executeAST(arg, null, processors));
            const errors = args.filter((arg) => arg.error) as ASTError[];
            if (errors.length > 0) {
                return {
                    error: true,
                    kind: 'FnExecutionError',
                    message: `AST function [${ast.fn}] has errors`,
                    stackedErrors: errors,
                };
            }
            try {
                /* 
                const processorFn = ast.fn
                if (!proccesorFn) return {
                    
                }
                */
                const appliedFn = processors[ast.fn](
                    ast,
                    ...(args as ASTValue<ASTArg>[]).map((arg) => arg.value),
                );

                if (Array.isArray(initialValue) && ast.multi) {
                    const results = initialValue.map(appliedFn);
                    const errors = results.filter((result) => result.error) as ASTError[];
                    if (errors.length > 0) {
                        return {
                            error: true,
                            kind: 'FnMultiplexError',
                            message: `AST function [${ast.fn}] has errors`,
                            stackedErrors: errors,
                        };
                    }
                    const ret = (results as ASTValue[])
                        .map((r) => r.value)
                        .filter((r) => r !== null);

                    return {
                        value: ast.flatten ? ret.flat() : ret,
                    };
                } else {
                    return appliedFn(initialValue);
                }
            } catch (e: any) {
                return {
                    error: true,
                    kind: 'FnExecutionError',
                    message: `AST function [${ast.fn}] has errors`,
                    stackedErrors: [
                        {
                            error: true,
                            kind: 'FnException',
                            message: `[${e}]: ${e?.message}`,
                        },
                    ],
                };
            }
        case undefined:
            return {
                error: true,
                kind: 'UnknownASTElementType',
                message: `ASTElement is \`undefined\``,
            };
        default:
            return {
                error: true,
                kind: 'UnknownASTElementType',
                message: `Value [${(ast as any)?.kind}] is not a valid ASTElementType`,
            };
    }
}
