import logger from '~B/utils/logger';
import { ASTArg, ASTElement, ASTPipeArg } from './types';

function get(ob: object | any[], path?: string) {
    if (!path) return ob;
    return path.split('.').reduce((way, s) => (!way || way[s] === undefined ? null : way[s]), ob);
}

export function split(ast: ASTElement, splitChar: ASTArg) {
    return (el: ASTPipeArg) => {
        if (typeof splitChar !== 'string' && !(splitChar instanceof RegExp)) {
            throw new Error('split(splitChar) should be a string or Regex');
        }
        const inputValue = `${el || ''}`;
        return {
            value: inputValue.split(splitChar),
        };
    };
}

export function firstContaining(ast: ASTElement, text: ASTArg) {
    return (el: ASTPipeArg) => {
        return {
            value:
                (el as HTMLElement[]).find((v) => {
                    const val = v instanceof HTMLElement ? v.textContent : v && `${v}`;
                    if (!val) return false;
                    return text instanceof RegExp ? val.match(text) : val.includes(text as string);
                }) || null,
        };
    };
}

export function selector(ast: ASTElement, query: ASTArg, pick: ASTArg = null) {
    return (el: ASTPipeArg) => {
        const htmlElement = (el instanceof HTMLElement ? el : document).querySelector(
            query as string,
        );
        return {
            value: pick !== null ? get(htmlElement, pick as string) : htmlElement,
        };
    };
}

export function global(ast: ASTElement, value: ASTArg) {
    return () => {
        return {
            value: get(window, value as string),
        };
    };
}

export function concat(ast: ASTElement, value1: ASTArg, value2: ASTArg) {
    return (el: ASTPipeArg) => {
        if (value1 === undefined) {
            throw new Error(`concat needs at least 1 argument`);
        }
        if (value2 === undefined) return { value: `${el || ''}${value1 || ''}` };
        return { value: `${value1 || ''}${value2 || ''}` };
    };
}

export function parse(ast: ASTElement, format?: 'json' | 'jsonld', pick?: string) {
    return (el: ASTPipeArg) => {
        let toParse = el instanceof HTMLElement ? el.textContent || '' : (el as string);
        switch (format) {
            case 'jsonld':
                toParse = toParse
                    .replaceAll('\n', '')
                    .replaceAll(/((?<!")http.+?(?!"))\s*?,/g, '"$1",')
                    .replaceAll(/:\s*'(.*?)'\s*,/g, ':"$1",')
                    .replaceAll(/,\s*\?*\s*("[a-zA-Z_0-9]*":)/g, ',$1');
            case 'json':
                return { value: get(JSON.parse(toParse), pick) };
        }
        throw new Error('Invalid argument, choose one of: [json,jsonld]');
    };
}

export function json_parse(ast: ASTElement, format: 'json' | 'jsonld' = 'json') {
    return (el: ASTPipeArg) => {
        let toParse = el as string;
        if (format === 'jsonld') {
            toParse = toParse
                .replaceAll('\n', '')
                .replaceAll(/((?<!")http.+?(?!"))\s*?,/g, '"$1",')
                .replaceAll(/:\s*'(.*?)'\s*,/g, ':"$1",')
                .replaceAll(/,\s*\?*\s*("[a-zA-Z_0-9]*":)/g, ',$1');
        }
        return { value: JSON.parse(toParse) };
    };
}

export function json_string(ast: ASTElement) {
    return (el: ASTPipeArg) => {
        return { value: JSON.stringify(el) };
    };
}

export function selectorAll(ast: ASTElement, query: ASTArg, pick: ASTArg = null) {
    return (el: ASTPipeArg) => {
        const htmlElements = [
            ...(el instanceof HTMLElement ? el : document).querySelectorAll(query as string),
        ].map((he: any) => ((he.toJSON = he.toString), he)) as HTMLElement[];
        return {
            value: pick !== null ? htmlElements.map((he) => get(he, pick as string)) : htmlElements,
        };
    };
}

function innerFormat(value: string, fmt: string) {
    if (value === null) {
        return null;
    }
    value = value || '';
    switch ((fmt as string).toLowerCase()) {
        case 'currency': {
            const currencySymbol = (value as string).replace(/((?!\$|||).)*/g, '');
            switch (currencySymbol[0]) {
                case '$':
                    return 'USD';
                case '':
                    return 'GBP';
                case '':
                    return 'CNY';
                case '':
                default:
                    return 'EUR';
            }
        }
        case 'number': {
            const findNumber = /((\.|,)?\d{1,3})+((\.|,)\d+)?/.exec(value as string);
            if (findNumber && findNumber[0]) {
                value = findNumber[0].replace(/(\.|,)(\d{3})/g, '$2');
            } else {
                return null;
            }
            const number = parseFloat(value.replace(',', '.'));
            return number;
        }
        case 'trim-string':
            return `${value || ''}`.trim();
        case 'string':
            return `${value || ''}`;
        case 'lower':
            return `${value}`.toLowerCase();
        case 'upper':
            return `${value}`.toUpperCase();
        default:
            return null;
    }
}

export function format(ast: ASTElement, fmt: ASTArg) {
    return (value: ASTPipeArg) => {
        return {
            value: innerFormat(value as any, fmt as string),
        };
    };
}

export function replace(ast: ASTElement, regex: ASTArg, format: ASTArg) {
    return (el: ASTPipeArg) => {
        return {
            value: (el as string).replace(regex as RegExp, format as string),
        };
    };
}

export function regex(ast: ASTElement, reg: ASTArg) {
    return (el: ASTPipeArg) => {
        const match = (reg as RegExp).exec(el as string);
        if (match && match.groups && match.groups.target) {
            return {
                value: match.groups.target,
            };
        } else {
            return {
                value: null,
            };
        }
    };
}

export function pick(ast: ASTElement, path: ASTArg) {
    return (el: ASTPipeArg) => {
        return {
            value: get(el as object, path as string),
        };
    };
}

export function flat(ast: ASTElement, levels: ASTArg = Infinity) {
    return (el: ASTPipeArg) => {
        return {
            value: (el as ASTPipeArg[]).flat(levels as number),
        };
    };
}

const stringRegex = /^('|")(.+)('|")$/;
const numberRegex = /^(([0-9])+(.[0-9]+)?)$/;
export function constant(value) {
    const arg = value.args[0].value;
    return (el) => {
        const ret = (() => {
            logger.debug('constant in', { arg });
            const stringMatch = stringRegex.exec(arg);
            if (stringMatch) {
                return arg.substring(1, arg.length - 1);
            }

            const match = numberRegex.exec(arg);
            if (match) {
                if (match[2]) {
                    return parseFloat(arg);
                } else {
                    return parseInt(arg);
                }
            }

            const vLower = arg.toLowerCase();
            switch (vLower) {
                case 'nan':
                    return NaN;
                case 'null':
                case 'none':
                    return null;
                case 'undefined':
                    return undefined;
                case 'true':
                case 'yes':
                case 'on':
                    return true;
                case 'false':
                case 'no':
                case 'off':
                    return false;
                default:
                    throw new Error(`Unparseable constant: ${arg}`);
            }
        })();
        logger.debug('constant out', { arg, ret });
        return { value: ret };
    };
}
