import { createAPI, metricsOptions } from '~/ui/utils/api';

const methods = {
    async metricView(metricsData: metricsOptions['body']) {
        this.metric({
            body: {
                name: metricsData.name,
                data: metricsData.data,
            },
            method: 'POST',
        });
    },
};

export const metricViews = createAPI<any, typeof methods>({
    name: 'metrics:views',
    endpoint: 'metric/',
    methods,
    read: () => {},
});
