import { createAPI } from '~/ui/utils/api';
import { IPriceTrack } from './price-track.types';

export const priceTrackAPI = createAPI({
    name: 'price-track',
    endpoint: 'price-track/',
    default: () => [],
    async read(): Promise<IPriceTrack[]> {
        return this.fetch('')
            .then((v) => v.json())
            .then((v) => v?.data || [])
            .then((v) => {
                return v.map((i: IPriceTrack) =>
                    typeof i.createdAt === 'string'
                        ? { ...i, createdAt: new Date(`${i.createdAt}Z`).valueOf() }
                        : i,
                );
            });
    },
    methods: {
        async create(element: IPriceTrack) {
            return this.fetch('', {
                method: 'POST',
                body: JSON.stringify(element),
            }).then((v) => v.json());
        },
        async remove(element: IPriceTrack) {
            return this.fetch('', {
                method: 'DELETE',
                body: JSON.stringify(element),
            }).then((v) => v.json());
        },
        async update(element: IPriceTrack) {
            return this.fetch('', {
                method: 'PATCH',
                body: JSON.stringify(element),
            }).then((v) => v.json());
        },
    },
});
