export type TimeableFn = (...args: any[]) => void;
export function throttle(callback: TimeableFn, waitMs: number) {
    let timerId: NodeJS.Timeout | null = null;
    let lastArgs: any[];
    return (...args: any[]) => {
        lastArgs = args;
        if (timerId) return;
        timerId = setTimeout(() => {
            callback(...lastArgs);
            timerId = null;
        }, waitMs);
    };
}

export function debounce(callback: TimeableFn, waitMs: number) {
    let timerId: NodeJS.Timeout | null = null;
    return (...args: any[]) => {
        timerId && clearTimeout(timerId);
        timerId = setTimeout(() => {
            callback(...args);
        }, waitMs);
    };
}

export async function sleepMs(waitMs: number) {
    return new Promise((r) => setTimeout(r, waitMs));
}
