import { createAPI } from '~/ui/utils/api';
import browser from '~B/browser/browser';
import { IUserSettings } from './user-settings.types';

const defaultSettings: IUserSettings = {
    initWithCompactMenu: true,
    theme: 'light',
    UIScale: 'default',
    SERPConf: 'activate',
    graphTimeRange: '6 months',
};

const settings_key = 'assistant_settings';

export const settingsAPIHandler = createAPI({
    name: 'user-settings',
    default() {
        return defaultSettings;
    },
    async read(): Promise<IUserSettings> {
        const settings = await browser.storage.sync
            .get([settings_key])
            .then((ob): IUserSettings => {
                return { ...defaultSettings, ...(ob[settings_key] || {}) };
            });

        return settings;
    },
    methods: {
        async update(settings: IUserSettings) {
            return browser.storage.sync.set({ [settings_key]: settings });
        },
    },
});
