type UTMSources =
    | 'extension_ofertapublicada'
    | 'extension_similares'
    | 'extension_cupones'
    | 'extension_ofertas';

export function addExtensionUTMParamsToLink(urlStr: string, source: UTMSources): string {
    let url = urlStr;
    url = updateURLParameter(url, 'utm_medium', 'extension');
    url = updateURLParameter(url, 'utm_source', source);
    url = updateURLParameter(url, 'utm_campaign', formatUTMDate(new Date()));
    return url;
}

function formatUTMDate(date: Date) {
    const y = date.getFullYear().toString();
    const m = (date.getMonth() + 1).toString();
    const d = date.getDate().toString();
    const formattedDate = `${y}-${m.padStart(2, '0')}-${d.padStart(2, '0')}`;
    return formattedDate;
}

function updateURLParameter(url: string, param: string, paramVal: string): string {
    var newAdditionalURL = '';
    var tempArray = url.split('?');
    var baseURL = tempArray[0];
    var additionalURL = tempArray[1];
    var temp = '';
    if (additionalURL) {
        tempArray = additionalURL.split('&');
        for (var i = 0; i < tempArray.length; i++) {
            if (tempArray[i].split('=')[0] != param) {
                newAdditionalURL += temp + tempArray[i];
                temp = '&';
            }
        }
    }

    var rows_txt = temp + '' + param + '=' + paramVal;
    return baseURL + '?' + newAdditionalURL + rows_txt;
}
