const ns = 'app/';

export const CLOSE_VIEW = `${ns}close-view`;
export const OPEN_VIEW = `${ns}open-view`;
export const SET_IS_SHOP_MATCH = `${ns}set-is-shop-match`;
export const SET_IS_PRODUCT_PAGE = `${ns}set-is-product-page`;
export const TOGGLE_MENU = `${ns}toggle-menu`;
export const APP_LOADING = `${ns}app-loading`;
export const SET_MENU_OPEN = `${ns}menu-open`;
export const SET_ICON_COUNT = `${ns}icon-count`;

export function closeView() {
    return {
        type: CLOSE_VIEW,
    };
}

export function openView(view: View) {
    return {
        type: OPEN_VIEW,
        payload: view,
    };
}

export function setIsShopMatch(activate: boolean) {
    return {
        type: SET_IS_SHOP_MATCH,
        payload: activate,
    };
}
export function setIsProductPage(activate: boolean) {
    return {
        type: SET_IS_PRODUCT_PAGE,
        payload: activate,
    };
}
export function setMenuOpen(open: boolean) {
    return {
        type: SET_MENU_OPEN,
        payload: open,
    };
}

export function setIconCount(payload: { icon: View; count: number }) {
    return {
        type: SET_ICON_COUNT,
        payload,
    };
}

export function toggleMenu() {
    return {
        type: TOGGLE_MENU,
    };
}

export function startAppLoading(payload: boolean) {
    return {
        type: APP_LOADING,
        payload,
    };
}

export interface IViewListing<TSimilar, TVoucher, TOffer> {
    similar: TSimilar;
    voucher: TVoucher;
    offer: TOffer;
}

export interface IView<TPrice, TSimilar, TVoucher, TOffer, TAccount, TPostedOffer>
    extends IViewListing<TSimilar, TVoucher, TOffer> {
    productStats: TPrice;
    alertas: TAccount;
    postedOffer: TPostedOffer;
}

export type View = 'productStats' | 'postedOffer' | 'similar' | 'voucher' | 'offer' | 'alertas';

export interface IViewListingSimple<T> extends IViewListing<T, T, T> {}
export interface IViewSimple<T> extends IView<T, T, T, T, T, T> {}

export interface IAppState {
    appLoading: boolean;
    isShopMatch: boolean;
    isProductPage: boolean;
    view: View | null;
    menuOpen: boolean;
    icons: IViewSimple<{ count: number; showCount: boolean }>;
}

const initialState: IAppState = {
    appLoading: false,
    isShopMatch: import.meta.env.VITE_NODE_ENV === 'test',
    isProductPage: false,
    view: null,
    menuOpen: false,
    icons: {
        offer: { count: 0, showCount: false },
        postedOffer: { count: 0, showCount: false },
        productStats: { count: 0, showCount: false },
        similar: { count: 0, showCount: false },
        voucher: { count: 0, showCount: false },
        alertas: { count: 0, showCount: true },
    },
};

export default function appReducer(state = initialState, action: any): IAppState {
    switch (action.type) {
        case CLOSE_VIEW:
        case OPEN_VIEW:
            return { ...state, view: action.payload || null };
        case SET_IS_SHOP_MATCH:
            return { ...state, isShopMatch: action.payload };
        case SET_IS_PRODUCT_PAGE:
            return { ...state, isProductPage: action.payload };
        case TOGGLE_MENU:
            return { ...state, menuOpen: !state.menuOpen };
        case APP_LOADING:
            return { ...state, appLoading: action.payload };
        case SET_MENU_OPEN:
            return state.isShopMatch ? state : { ...state, menuOpen: action?.payload };
        case SET_ICON_COUNT:
            return {
                ...state,
                icons: {
                    ...state.icons,
                    [action?.payload?.icon]: {
                        ...state?.icons?.[action?.payload?.icon],
                        count: action?.payload?.count,
                    },
                },
            };
        default:
            return state;
    }
}
