import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { addExtensionUTMParamsToLink } from '~/ui/utils/UTMLinks';
import { IVoucherState } from './voucher.types';

const initialState: IVoucherState = {
    shop: '',
    vouchers: [],
    codeUnlocked: false,
};

const voucherSlice = createSlice({
    name: 'voucher',
    initialState,
    reducers: {
        setVoucher: (state, action: PayloadAction<IVoucherState>) => {
            action.payload.vouchers.forEach((v) => {
                if (v.url) v.url = addExtensionUTMParamsToLink(v.url, 'extension_cupones');
            });
            return action.payload;
        },
        setUnLock(state, action: PayloadAction<boolean>) {
            state.codeUnlocked = action.payload;
        },
    },
});

export default voucherSlice.reducer;
export const { setVoucher, setUnLock } = voucherSlice.actions;
