import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IAccountState, ILoginStatus } from './account.types';

function asyncHelper() {
    const initialState: IAccountState =
        import.meta.env.VITE_NODE_ENV === 'test'
            ? {
                  loginStatus: {
                      loggedIn: true,
                      username: 'luismedina',
                  },
              }
            : {
                  loginStatus: {
                      loggedIn: false,
                      username: '',
                  },
                  logInLoader: false,
              };

    const accountSlice = createSlice({
        name: 'account',
        initialState,
        reducers: {
            setLoginStatus(state, action: PayloadAction<ILoginStatus>) {
                state.loginStatus = action.payload;
            },
            setLogInLoader(state, action: PayloadAction<boolean>) {
                state.logInLoader = action.payload;
            },
        },
    });
    return accountSlice;
}

export default asyncHelper().reducer;
export const { setLoginStatus, setLogInLoader } = asyncHelper().actions;
