import moment from 'moment';
import { IPriceState, IProductInfo } from './product-stats.types';

export function calculateStats(state: IPriceState) {
    if (!state.product || !state.history.length) return;

    state.stats.meanPrice = getMean(
        state.product,
        {
            count: 3,
            value: 'months',
        },
        state.history,
    );
    state.stats.price = state.history[state.history.length - 1].price;
    state.stats.maxPrice = Math.max(...state.history.map((v) => v.price));
    state.stats.minPrice = Math.min(...state.history.map((v) => v.price));
}

const getMeanCache: { [key: string]: number } = {};
function getMean(
    product: IProductInfo,
    interval: { count: number; value: 'months' | 'days' },
    priceHistory: { price: number; time: number }[],
): number {
    let cacheId = `${product.id}${product.shop}${product.price}${
        priceHistory ? priceHistory[0] && priceHistory[0].time : 'null'
    }`;
    if (priceHistory.length === 0) {
        return product.price;
    } else if (cacheId in getMeanCache) {
        return getMeanCache[cacheId];
    }

    let price = product.price;

    let startRange = moment().add(-Math.abs(interval.count), interval.value).valueOf();
    let endRange = moment().valueOf();
    let acc = 0,
        totalSpan = 0;
    let history = [
        ...priceHistory,
        {
            price: price !== null ? price : priceHistory[priceHistory.length - 1].price,
            time: moment().valueOf(),
        },
    ]; //priceHistory.length > 0 ? [...priceHistory, {...priceHistory[priceHistory.length]}]
    for (
        // init
        let i = history.findIndex((v) => v.time > startRange),
            curr = history[i] || { ...history[i - 1] },
            prev = { ...(history[i - 1] || history[i]), time: startRange };
        // condition
        i <= history.length;
        // loop
        i++, curr = history[i] || { ...history[i - 1], time: endRange }, prev = history[i - 1]
    ) {
        let timeSpan = curr.time - prev.time;
        let spanMean = timeSpan * prev.price;
        acc += spanMean;
        totalSpan += timeSpan;
    }
    const result = parseFloat((acc / totalSpan).toFixed(2));
    getMeanCache[cacheId] = result;
    return result;
}
