import { throttle } from './timing.utils';

function createObserveChangesCallback(
    changeDetected: (mutations: MutationRecord[]) => void,
    throttlingMs: number = 0,
) {
    const throttledFn = throttle(changeDetected, throttlingMs);

    return function observeChangesCallback(
        mutationsList: MutationRecord[],
        observer: MutationObserver,
    ) {
        throttledFn(mutationsList);
    };
}

export function createMutationObserver(
    defaultSelector: string,
    defaultCallback: (mutations: MutationRecord[]) => void,
    throttleTimeMs = 1000,
) {
    let isObserving = false;
    let selector = defaultSelector;
    let callback = defaultCallback;
    let config: MutationObserverInit = {
        childList: true,
        subtree: true,
        attributes: true,
    };

    const observer = new MutationObserver(
        createObserveChangesCallback((m) => {
            callback(m);
        }, throttleTimeMs),
    );

    const ob = {
        observe() {
            document.querySelectorAll(selector).forEach((node) => observer.observe(node, config));
            isObserving = true;
        },
        changeConfig(newConfig: MutationObserverInit) {
            config = newConfig;
            this.resetObservation();
        },
        changeSelector(newSelector: string) {
            selector = newSelector;
            this.resetObservation();
        },
        changeCallback(newCallback: (mutations: MutationRecord[]) => void) {
            callback = newCallback;
            this.resetObservation();
        },
        disconnect() {
            observer.disconnect();
            isObserving = false;
        },
        resetObservation() {
            observer.disconnect();
            if (isObserving) {
                this.observe();
            }
        },
    };

    return ob;
}

