export type AuthBody = {
    version: number;
    extensionId: string;
    clientId: string;
    username?: string;
    password?: string;
};

export interface ScrappedData {
    [key: string]: number | string | null | undefined | boolean;
}

export interface ScrapperConfig {
    lastPrice: string[];
    currency: string[];
    ean: string[];
    id: string[];
    imageUrl: string[];
    price: string[];
    seller: string[];
    shippingPrice: string[];
    title: string[];
    url: string[];
}

export enum ExtensionScrappingWorkingMode {
    default = 'default',
    onlyId = 'onlyId',
    disabled = 'disabled',
}

export interface ShopScrappingDetails {
    id: string;
    urlMatch: string;
    scrapperConfig: ScrapperConfig;
    detailPage: boolean;
    shop: string;
    workingMode?: ExtensionScrappingWorkingMode;
}

export type ExtensionConfig = {
    scrapConfig: ShopScrappingDetails[];
};

/* type ScrapProcess<T> = {
    [key in keyof T]: T[key];
} & { matcher: RegExp }; */

export interface ShopDetailMatcher extends ShopScrappingDetails {
    matcher: RegExp;
}

//? TODO up to concrete until we have confirmation about specific request headers in session endpoint
export type Headers = {
    [key: string]: string | number;
};

export type Action<T = any> = { type: string; payload: T };

export interface SerpShops {
    data: {
        shops: {
            merchantDomain: null | string;
            shopId: string;
            stats: {
                offersCount: number;
                vouchersCount: number;
            };
        }[];
    };
}
